import React, { FC } from 'react';
import styles from './rules.module.scss'
import cn from 'classnames';

const Rules: FC = () => {
  return (
    <div>
      <div className={cn(styles.rules)}>
        <h3>Rules</h3>
        <ol>
          <li>Enter your name and press the START button.</li>
          <li>Guess under which piece of pizza your name is hidden!</li>
          <li>If you guessed right, take your pizza for free üòç</li>
        </ol>
        <p>* The promotion is valid 1 time for 1 pizza order of any size ;)</p>
      </div>
    </div>
  );
};

export default Rules;
