import React, { FC, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { actions } from '../../../store/game/slice';
import { useAppDispatch } from '../../../store/hooks';
import styles from './start-form.module.scss'
import cn from 'classnames';
import Button from '../../../common/components/button';

const StartForm: FC = () => {
  const [inputValue, setInputValue] = useState<string>('');
  const dispatch = useAppDispatch();
  const history = useHistory();
  

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(event.target.value);
  };

  const handleStart = () => {
    dispatch(actions.setName(inputValue));
    history.push('/en/game');
  };

  return (
    <div>
      <form onSubmit={() => { handleStart() }}>
        <div className={cn(styles.main)}>
          <input required value={inputValue} onChange={handleChange} type="text" placeholder="Enter your name" />
          <Button>ENTER</Button>
        </div>
      </form>
    </div>
  );
};

export default StartForm;
